import $utils from '../../utils/utils'
import {ValidMixin} from "../../mixin/component-mixin";

export const ColumnMixin = {
    mixins: [ValidMixin],
    props: {
        title: {type: String},                                                                                          //
        field: {type: String},                                                                                          //
        width: {default: 200},                                                                                          //
        fit: {type: Number, default: 0},                                                                                //
        order: {type: Number, default: 0},                                                                              //
        fixed: {type: String, default: 'center', validator: val => $utils.oneOf(val, ['left', 'center', 'right'])},     //
        search: {type: Boolean, default: true},                                                                         //
        sort: {type: Boolean, default: true},                                                                           //
        quickFilter: {type: Boolean, default: false},                                                                   //
        filterName: {type: String, default: 'input'},                                                                   //
        filterOption: {type: Object},                                                                                   //
        lov: {type: String},                                                                                            //
        placeLeft: {type: Boolean},                                                                                     //
        placeRight: {type: Boolean},                                                                                    //
        align: {type: String, default: 'left'},                                                                         //
        editable: {type: Boolean, default: true},                                                                       //
        hide: {type: Boolean},                                                                                          //
        disabledConfig: {type: Boolean},                                                                                //

        dataType: {type: String},                                                                                       //:tel,cny,money,percent
        tooltip: {type: Boolean},                                                                                       //tooltip
        link: {type: Boolean},                                                                                          //
        clickWhenIneditable: {type: Boolean, default: true},                                                            //
        showInDialog: {type: Boolean},                                                                                  //dialog

        editableFunc: {type: Function},                                                                                 //
        requiredFunc: {type: Function},                                                                                 //
    },
    computed: {
        col() {
            return this.$refs.column.col;
        },
        columnBinding() {
            return {
                title: this.title,
                field: this.field,
                width: this.width,
                fit: this.fit,
                order: this.order,
                fixed: this.fixed,
                search: this.search,
                sort: this.sort,
                quickFilter: this.quickFilter,
                filterName: this.filterName,
                filterOption: this.filterOption,
                lov: this.lov,
                placeLeft: this.placeLeft,
                placeRight: this.placeRight,
                align: this.align,
                hide: this.hide,
                disabledConfig: this.disabledConfig,

                editable: this.editable,
            }
        },
        columnItemBinding() {
            return {
                field: this.field,
                editable: this.editable,
                align: this.align,
                dataType: this.dataType,
                tooltip: this.tooltip,
                link: this.link,
                scopeSlotFunc: this.$scopedSlots.default,
                required: this.required,
                rules: this.rules,
                validOnInit: this.validOnInit,
                editableFunc: this.editableFunc,
                requiredFunc: this.requiredFunc,
            }
        },
    },
    methods: {
        /**
         * 
         * @author  
         * @date    2019/1/10 17:45
         */
        p_clickItem({row, editRow, rowIndex, field, editable}) {
            if (!editable && this.showInDialog) this.$dialog.show(row[field])
            if (!!this.clickWhenIneditable && !!editable) return
            this.$emit('click', {row, editRow, rowIndex, field, editable})
        },
    }
}

export const ColumnItemMixin = {
    mixins: [ValidMixin],
    props: {
        row: {},                                                                                                        //
        editRow: {},                                                                                                    //
        rowIndex: {},                                                                                                   //
        col: {},                                                                                                        //
        colIndex: {},                                                                                                   //
        field: {},                                                                                                      //
        editable: {type: Boolean, default: true},                                                                       //
        align: {type: String, default: 'left'},                                                                         //
        dataType: {type: String},                                                                                       //:tel,cny,money,percent
        tooltip: {type: Boolean},                                                                                       //tooltip
        link: {type: Boolean},                                                                                          //
        scopeSlotFunc: {type: Function},                                                                                //

        editableFunc: {type: Function},                                                                                 //
        requiredFunc: {type: Function},                                                                                 //
    },
    data() {
        return {
            currentEditable: false,                                                                                     //
            _p_row: null,
        }
    },
    computed: {
        /**
         * ROW
         * @author  
         * @date    2019/1/8 10:21
         */
        p_row() {
            if (!this._p_row) this._p_row = this.$plain.$dom.findComponentUpward(this, 'pl-table-row')
            return this._p_row
        },
        /**
         * 
         * @author  
         * @date    2019/1/10 15:31
         */
        p_editable() {
            if (this.rowIndex == null && this.colIndex == null) return
            const editable = this.currentEditable && this.editable
            return editable && (!this.editableFunc || this.editableFunc(this.p_data))
        },
        /**
         * 
         * @author  
         * @date    2019/1/18 15:59
         */
        p_required() {
            if (this.rowIndex == null && this.colIndex == null) return
            return !!this.requiredFunc ? this.requiredFunc(this.p_data) : this.required
        },
        /**
         * 
         * @author  
         * @date    2019/1/18 16:35
         */
        p_data() {
            return {
                row: this.row,
                editRow: this.editRow,
                rowIndex: this.rowIndex,
                field: this.field
            }
        },
        /**
         * row
         * @author  
         * @date    2019/1/11 10:52
         */
        showRow() {
            return this.p_editable ? this.editRow : this.row
        },
    },
    mounted() {
        this.rowIndex != null && this.colIndex != null && this.p_row.p_add(this)
    },
    beforeDestroy() {
        !!this.p_row && this.p_row.p_remove(this)
    },
    methods: {
        /**
         * 
         * @author  
         * @date    2019/1/8 10:21
         */
        enableEdit() {
            this.currentEditable = true
        },
        /**
         * 
         * @author  
         * @date    2019/1/8 10:21
         */
        disableEdit() {
            this.currentEditable = false
        },
    }
}